import React from "react";
import { dateFormat } from "../util"
// import "..Table/Tale_"
import { db, db_name } from "../Database/Firebase";
import { collection, query, orderBy } from "firebase/firestore";
import { getDocs } from "firebase/firestore";


class MonthlyTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            datePicker: {year: 2014, month: 11},
            table_contents: [],
        };
    }
    async UNSAFE_componentWillMount() {
        const q = query(collection(db, db_name), orderBy("date", "desc"));
        const querySnapshot = await getDocs(q);
        querySnapshot.forEach((doc) => {
            // doc.data() is never undefined for query doc snapshots
            console.log(doc.id, " => ", doc.data());

            this.setState((prevState) => ({
                table_contents: [...prevState.table_contents, Object.assign(doc.data(), { 'id': doc.id })]
            }));
        });

    }
    render() {
        const pickerLang = {
            months: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            from: 'From', to: 'To',
        }
        const { datePicker } = this.state;
    
        const makeText = m => {
            if (m && m.year && m.month) return (pickerLang.months[m.month-1] + '. ' + m.year)
            return '?'
        }
        return (
            <div>
                <table className="styled-table">
                    <thead>
                        <tr>
                            <th>Transaction Type</th>
                            <th>Name</th>
                            <th>Amount</th>
                            <th>From</th>
                            <th>To</th>
                            <th>Date</th>
                            <th>Budget Item</th>
                            <th>Notes</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.table_contents.map((item) => (<tr key={item.id}>
                                <td>{item.transaction_type}</td>
                                <td>{item.name}</td>
                                <td>{"$ "+Number(item.amount).toFixed(2)}</td>
                                <td>{item.from}</td>
                                <td>{item.to}</td>
                                <td>{dateFormat(item.date,"longDate")}</td>
                                <td>{item.budget_item}</td>
                                <td>{item.notes}</td>
                            </tr>))
                        }
                    </tbody>
                </table>
            </div>
        );
    }
}

export default MonthlyTable;